<template>
  <MovieModal
    v-if="showModal"
    :movieID="clickedMovieID"
    @close="toggleModal()"
  />
  <h1 class="headerTitle">Movie Search</h1>
  <p class="byLine">
    Endre Jordal 2022<br />Made with
    <a href="https://vuejs.org/" target="_blank" class="vueLink">vue.js</a>
    powered by
    <a class="tmdblink" href="https://www.themoviedb.org/" target="_blank"
      >TMDB</a
    >
  </p>
  <div class="wrap">
    <div class="search">
      <input
        type="text"
        class="searchTerm"
        placeholder="Search.."
        @keyup.enter="callSearch(searchInput)"
        v-model="searchInput"
      />
      <button @click="callSearch(searchInput)" class="searchButton">
        <i class="fa fa-search"></i>
      </button>
    </div>
  </div>

  <table class="table" v-show="searchResults">
    <thead>
      <tr>
        <th class="tHead">Poster</th>
        <th class="tHead">Title</th>
        <th class="tHead">Synopsis</th>
        <th class="tHead">Rating</th>
        <th class="tHead">Release Year</th>
      </tr>
    </thead>
    <tbody>
      <tr class="movieRows" v-for="result in searchResults" :key="result.id">
        <td class="moviePoster" @click="toggleModal(result.id)">
          <div class="linkOverlay"></div>
          <img
            v-if="result.backdrop_path"
            :src="`https://image.tmdb.org/t/p/w300${result.backdrop_path}`"
            alt=""
          />

          <img v-if="!result.backdrop_path" src="./assets/missing.png" alt="" />
        </td>
        <td class="movieTitle" @click="toggleModal(result.id)">
          {{ result.original_title }}
        </td>
        <td class="movieSynopsis" @click="toggleModal(result.id)">
          <div class="synText">
            {{ result.overview }}
          </div>
        </td>
        <td class="movieRating" @click="toggleModal(result.id)">
          <div class="rateContainer">
            <p class="ratenumber" :class="ratingColor(result.vote_average)">
              {{ result.vote_average }} <span class="ofTen">/ 10</span>
            </p>

            <p class="votes" style="font-size: 0.6rem">
              ({{ result.vote_count }} votes)
            </p>
          </div>
        </td>

        <td class="movieReleaseyear">{{ result.release_date.slice(0, 4) }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import MovieModal from "./components/MovieModal.vue";

export default {
  name: "App",
  components: { MovieModal },
  data() {
    return {
      clickedMovieID: null,
      showModal: false,
      searchInput: "",
      key: "e270661878ba58b07fd3d2b60b2cf8c6",
      searchResults: "",
    };
  },
  methods: {
    callSearch(term) {
      fetch(
        `https://api.themoviedb.org/3/search/movie?api_key=${this.key}&query=${term}`
      )
        .then((res) => res.json())
        .then((data) => (this.searchResults = data.results))
        .catch((err) => console.log(err));
    },
    toggleModal(id) {
      this.clickedMovieID = id;
      this.showModal = !this.showModal;
    },
    ratingColor(rating) {
      if (rating > 8) {
        return "highest";
      } else if (rating > 7) {
        return "high";
      } else if (rating > 5) {
        return "medium";
      } else {
        return "low";
      }
    },
  },
};
</script>

<style>
@import "./style.css";
</style>
