<template>
  <div class="modalDim" @click="closeModal()">
    <div class="modalcontainer">
      <img
        v-if="movieDetails.backdrop_path"
        :src="`https://image.tmdb.org/t/p/w400${movieDetails.backdrop_path}`"
        alt=""
        class="dPoster"
      />
      <p class="dTitle">
        {{ movieDetails.original_title }}
      </p>
      <span
        v-for="genre in movieDetails.genres"
        class="dGenres"
        :key="movieDetails.genres"
      >
        <em> {{ genre.name }} </em>
      </span>
      <p class="dRuntime">{{ movieDetails.runtime }} minutes</p>
      <p class="dYear">Released: {{ movieDetails.release_date }}</p>
      <p class="rating"></p>
      <p class="dSyn">{{ movieDetails.overview }}</p>
      <p class="dRating" :class="ratingColor()">
        {{ movieDetails.vote_average }} <span class="ofTen">/ 10</span>
      </p>
      <p class="dVotes">
        <em>({{ movieDetails.vote_count }} votes)</em>
      </p>
      <a
        class="imdblink"
        :href="`https://www.imdb.com/title/${movieDetails.imdb_id}`"
        target="_blank"
        >IMDB Link</a
      >
      <p></p>
    </div>
  </div>
</template>

<script>
export default {
  name: "MovieModal",
  props: {
    movieID: Number,
  },
  data() {
    return {
      movieDetails: "",
      key: "e270661878ba58b07fd3d2b60b2cf8c6",
    };
  },

  methods: {
    closeModal() {
      this.$emit("close");
    },
    ratingColor() {
      if (this.movieDetails.vote_average > 8) {
        return "highest";
      } else if (this.movieDetails.vote_average > 7) {
        return "high";
      } else if (this.movieDetails.vote_average > 5) {
        return "medium";
      } else {
        return "low";
      }
    },
  },
  mounted() {
    fetch(
      `https://api.themoviedb.org/3/movie/${this.movieID}?api_key=${this.key}`
    )
      .then((res) => res.json())
      .then((data) => (this.movieDetails = data))
      .catch((err) => console.log(err));
  },
};
</script>

<style>
@import "../style.css";
</style>
